<script lang="ts">
import { defineComponent, computed, onMounted, watchEffect, ref } from 'vue';
import { useDatabaseStore } from '@/store/totalData';
import { useRouter } from 'vue-router';

export default defineComponent({
  components: {
    // NavBar
  },
  setup() {
    const databaseStore = useDatabaseStore();
    const router = useRouter();
    
    const totalData = ref<any>([]);
    watchEffect (() => {
      totalData.value = [
        {title:"", allCount: databaseStore.getMiniData.length, count: databaseStore.getMiniCount, icon:"", color:"#d1e3ff", type:"mini"},
        {title:"", allCount: databaseStore.getRideData.length, count: databaseStore.getRideCount, icon:"", color:"#d1e3ff", type:"ride"},
        {title:"", allCount: databaseStore.getMusicData.length, count: databaseStore.getMusicCount, icon:"", color:"#d1e3ff", type:"music"},
        {title:"", allCount: databaseStore.getEmotionData.length, count: databaseStore.getEmotionCount, icon:"", color:"#d1e3ff", type:"emotion"},
        {title:"", allCount: databaseStore.getHairData.length, count: databaseStore.getHairCount, icon:"", color:"#d1e3ff", type:"hair"},
        {title:" ", allCount: databaseStore.getChocoboData.length, count: databaseStore.getChocoboCount, icon:"", color:"#d1e3ff", type:"chocobo"},
        {title:" ", allCount: databaseStore.getFashionData.length, count: databaseStore.getFashionCount, icon:"", color:"#d1e3ff", type:"fashion"},
        {title:"", allCount: databaseStore.getPortraitData.length, count: databaseStore.getPortraitCount, icon:"", color:"#d1e3ff", type:"portrait"},
    ];
    })

    function roundToTwoDecimalPlaces(num:any) {
      //    
      const roundedNum = Math.round(num * 100) / 100;
      
      //     
      if (Number.isInteger(roundedNum)) {
          return roundedNum.toString();
      }
      
      //    
      const roundedStr = roundedNum.toString();
      if (roundedStr.split('.')[1].length === 1) {
          return roundedStr;
      }

      //   
      return roundedNum.toFixed(2);
    }

    function goPage(type:string) {
      router.push({'name':type});
    }
    
    return {
      totalData,
      roundToTwoDecimalPlaces,
      goPage
    };
  },
});
</script>
